import React, { useEffect, useState } from "react";
import CourseCard from "./course";
import { Grid } from "@material-ui/core";
import coursesContent from "./courseContent";
import { makeStyles } from "@material-ui/core/styles";
import FormControl from "@material-ui/core/FormControl";
import Axios from "axios";

const useStyles = makeStyles((theme) => ({
  button: {
    display: "block",
    marginTop: theme.spacing(2),
  },
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120,
  },
  breadcrumbs: {
    "& > * + *": {
      marginTop: theme.spacing(2),
    },
  },
}));

const Content = () => {
  const classes = useStyles();
  const [error, setError] = useState();
  const [coures, setCoures] = useState([]);

  useEffect(() => {
    const getCourses = async () => {
      try {
        const coursesRes = Axios.get(
          "https://alpha-school.herokuapp.com/courses"
        );
        setCoures((await coursesRes).data.data);
      } catch (err) {
        err && setError(err);
        console.log(err);
      }
    };

    getCourses();
  }, []);

  const getcourseContent = (coursesObejct) => {
    return (
      <Grid item xs={12} sm={4}>
        <CourseCard {...coursesObejct} />
      </Grid>
    );
  };
  const courseContentUnique = [
    ...new Map(coures.map((item) => [item["courseName"], item])).values(),
  ];
  return (
    <div>
      <FormControl
        variant="filled"
        className={classes.formControl}
      ></FormControl>
      <Grid container spacing={2}>
        {courseContentUnique.map((coursesObejct) =>
          getcourseContent(coursesObejct)
        )}
      </Grid>
    </div>
  );
};

export default Content;
