const CoursesByChapter = () => {
  const location = useLocation();
  const classes = useStyles();

  const getcourseContent = (coursesObejct) => {
    {
      console.log(coursesObejct);
    }
    return (
      <Grid item xs={12} sm={4}>
        <Card style={{ marginTop: "20%" }}>
          <CardHeader
            title={`Chapitre N° ${coursesObejct.chapter}`}
            subheader={coursesObejct.chapterName}
          />

          <CardActions>
            <Link
              to={{
                pathname: `/coursePage/${coursesObejct.idCourse}`,
                state: {
                  idCourse: coursesObejct.idCourse,
                  courseName: coursesObejct.courseName,
                  chapter: coursesObejct.chapter,
                  chapterName: coursesObejct.chapterName,
                  description: coursesObejct.description,
                  classe: coursesObejct.classe,
                  classeSection: coursesObejct.classeSection,
                  vedioUrl: coursesObejct.vedioUrl,
                  pdfUrl: coursesObejct.pdfUrl,
                },
              }}
              style={{ textDecoration: "none" }}
            >
              <Button color="primary">Afficher le cours</Button>
            </Link>
          </CardActions>
        </Card>
      </Grid>
    );
  };
  return (
    <div>
      <FormControl
        variant="filled"
        className={classes.formControl}
      ></FormControl>
      <Grid container spacing={2}>
        {coursesContent.map((coursesObejct) =>
          location.state.courseName === coursesObejct.courseName
            ? getcourseContent(coursesObejct)
            : ""
        )}
      </Grid>
    </div>
  );
};

export default CoursesByChapter;
